import { Component, ElementRef, OnInit, ViewChild } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { LoadingController, ModalController, NavController, PopoverController } from '@ionic/angular';
import { BehaviorSubject, combineLatest } from 'rxjs';
import { ActivatedRoute } from '../../../../../node_modules/@angular/router';
import { AdvertClient, AdvertEntity, ChatClient, ChatDetailsEntity, CreateMessageRequestModel, EntityTypeEnum, MessageClient, MessageResponse, MessageTypeEnum, StatusEnum, UserClient, UserModel } from '../../../clients/bonobogive.client';
import { AuthService } from '../../../services/auth.service';
import { ErrorService } from '../../../services/error.service';

@Component({
  selector: "chat",
  templateUrl: './chat.page.html',
  styleUrls: ['./chat.page.scss'],
})
export class ChatPage implements OnInit {

  // googleMapApiKey = "AIzaSyBL_Jz0y3Y2BNMDn677sRvMRWhW1_PrQ4o";
  title = 'Post Private Message';
  // conversationId: string;
  advertPrimaryKey: string;
  advertPartitionKey: string;
  advertSortKey: string;
  chatPrimaryKey: string;
  chatDetails: ChatDetailsEntity;
  myUserProfile: UserModel;
  theirUserProfile: UserModel;
  postMessageForm: FormGroup;
  loadingMedia = false;
  // messages: MessageEntity[] = [];
  gotAllMessages = false;
  messages: MessageResponse[] = [];

  advertImageUrl = "";
  myAdvert = false;

  @ViewChild('conversationheader')
  conversationheaderView: ElementRef;
  @ViewChild('content') private content: any;
  // @ViewChild(Content)
  // content: Content;
  @ViewChild('conversationFooter')
  conversationFooterView: ElementRef;
  conversationheaderHeight: number;
  conversationFooterHeight: number;

  messgeInputPlaceholder = "Message privately";

  take = 10;
  limit = new BehaviorSubject<number>(this.take); // import 'rxjs/BehaviorSubject';
  lastLatestMessageKey: string;
  advert: AdvertEntity;


  showRatingsCount = true;
  allowRating = false;
  getReviewerRating = true;
  readOnlyRating = true;

  average = 0;
  chatSeen = false;

  loadingMessages = false;
  loadingMore: boolean = false;
  loadingChatDetails = false;

  nextRequestBlob = "";

  constructor(public auth: AuthService,
    private readonly formBuilder: FormBuilder,
    public errorService: ErrorService,
    public loadingCtrl: LoadingController,
    public navController: NavController,
    public popoverCtrl: PopoverController,
    private activatedRoute: ActivatedRoute,
    public modalController: ModalController,
    private chatClient: ChatClient,
    private messageClient: MessageClient,
    private authService: AuthService,
    private userClient: UserClient,
    private advertClient: AdvertClient) {

    this.postMessageForm = this.formBuilder.group({
      message: ['', Validators.required]
    });


  }

  StatusEnum = StatusEnum;

  get isAdvertOwner() {
    if (this.advert && this.advert.userId === this.auth.user.uid) {
      return true;
    }
    return false;
  }


  messageInputFocus() {
    this.scrollToBottom();
  }

  messageInputBlur() {
    this.scrollToBottom();
  }

  ngOnInit() {

    this.myUserProfile = this.authService.user;

    const urlParams = combineLatest(
      this.activatedRoute.params,
      this.activatedRoute.queryParams,
      (params, queryParams) => ({ ...params, ...queryParams }),
    );
    urlParams.subscribe(routeParams => {
      if (!routeParams.chatId) {
        return;
      }
      this.chatPrimaryKey = routeParams.chatId;
      this.loadChat();


      this.advertPartitionKey = this.chatPrimaryKey.split("chat.")[1].split("|")[0];

      this.advertSortKey = this.chatPrimaryKey.split("|")[1];

      this.advertClient.get(this.advertPartitionKey, this.advertSortKey).subscribe(res => {
        this.advert = res;
      })




    });
  }



  loadPreviousMessages(infiniteScroll) {
    if (!this.loadingChatDetails && !this.loadingMessages && (this.nextRequestBlob !== "" && this.nextRequestBlob))
      this.loadMessages(infiniteScroll);

    infiniteScroll.target.complete();
  }



  loadMessages(infiniteScroll) {
    this.loadingMessages = true;
    this.messageClient.get(this.chatPrimaryKey, MessageTypeEnum.Private, 10, this.nextRequestBlob).subscribe(async res => {
      if (infiniteScroll) {
        infiniteScroll.target.complete();
      }
      this.nextRequestBlob = res.lastEvaluatedKeyHash;
      if (res.lastEvaluatedKeyHash === "null" || !res.lastEvaluatedKeyHash) this.nextRequestBlob = "";
      this.messages.unshift(...res.messages);

      // TODO - update message seen or chat conversation seen

      this.chatClient.updateUserChatSeen(`chat.user-${this.auth.user.uid}`, this.chatDetails.primaryKey, true).subscribe(res => {
        console.log("Updated chat user to seen", res);
      });


    }).add(() => {
      this.loadingMessages = false;
    });

  }

  private loadChat() {
    this.loadingChatDetails = true;
    this.chatClient.get(this.chatPrimaryKey.split('|')[0] + "|" + this.chatPrimaryKey.split('|')[1], this.chatPrimaryKey.split('|')[2]).subscribe(res => {
      this.chatDetails = res;

      // their user
      var theirUserId = this.chatDetails.userIds.find(x => x !== this.authService.user.uid);

      if (!theirUserId) {
        theirUserId = this.chatDetails.userIds[0];
      }

      this.userClient.get(theirUserId, true).subscribe(res => {
        this.theirUserProfile = res;
      });

      this.loadMessages(null);
    }).add(() => {
      this.loadingChatDetails = false;
      this.scrollToBottom();
    });
  }


  private scrollToBottom() {
    setTimeout(() => {
      try {
        if (this.content) { this.content.scrollToBottom(); }
      }
      catch (e) {

      }
    });
  }

  async privateMessageForm() {
    var messageContent = this.postMessageForm.value['message'];

    var message = new CreateMessageRequestModel();
    message.entityPrimaryKey = this.chatDetails.primaryKey;
    message.messageType = MessageTypeEnum.Private;
    message.text = messageContent;
    message.userId = this.auth.user.uid;
    message.entityType = EntityTypeEnum.Chat;


    this.messageClient.post(message).subscribe(res => {

      var messageResponse = new MessageResponse();
      messageResponse.message = res;
      messageResponse.user = this.auth.user;
      this.messages.push(messageResponse);

    }).add(() => {
      this.postMessageForm.reset();
    });


  }

  openAdvertStatus() {
    this.navController.navigateForward("advert-status/" + this.advert.primaryKey);
  }

  // async updateAdvertStatus() {

  //   await this.advertService.updateAdvertStatus(this.advert, this.advert.status);
  //   if (this.advert.status === 'collected') {
  //     this.rateUser();

  //   }
  // }

  goToProfile(userId: string) {
    this.navController.navigateForward(`user-profile/${userId}`);
  }

  goToAdvert(partitionKey: string, sortKey: string) {
    this.navController.navigateForward(`advert/${partitionKey}/${sortKey}`);
  }

  async rateUser() {

    const data = {
      reviewerUserId: this.auth.user.uid,
      ratedUserId: this.theirUserProfile.uid,
      overallaverages: false,
      advertId: this.chatDetails.partitionKey,
      getAverageUserRating: false,
      getReviewerRating: true,
      readOnlyRating: false,
      showRatingsCount: false,
      displayname: this.theirUserProfile.name
    };

    var url = `rating?ratedUserId=${data.ratedUserId}&reviewerUserId=${data.reviewerUserId}&overallaverages=${data.overallaverages}&advertId=${data.advertId}&getAverageUserRating=${data.getAverageUserRating}&getReviewerRating=${data.getReviewerRating}&readOnlyRating=${data.readOnlyRating}&showRatingsCount=${data.showRatingsCount}&displayname=${data.displayname}`;
    console.log("Navigate to " + url);

    this.navController.navigateForward(url);

  }

}
